.advantages {
  background-color: var(--dark-color);
  padding-top: 72px;
  padding-bottom: 117px;
  @include mobile {
    padding-top: 50px;
    padding-bottom: 50px;
  }
  &__subtitle {
    padding: 77px 63px;
    border-radius: 525.114px;
    border: 1.75px solid rgba(255, 255, 255, 0.6);
    opacity: 0.7;
    margin: 0;
    color: var(--light-color);
    text-align: center;
    font-family: Forum;
    font-size: 40px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin-bottom: 148px;
    transition: color 0.3 ease-in-out, border 0.3 ease-in-out;

    &:hover {
      color: var(--blue-color);
      border: 1.75px solid var(--blue-color);
    }

    @include small-tablet {
      font-size: 35px;
    }

    @include mobile {
      padding: 17px 20px;
      font-size: 18px;
      border-radius: 525.114px;
      border: 1.75px solid var(--06, #fff);
      opacity: 0.7;
      margin-bottom: 40px;
    }
  }
  &__title {
    margin: 0;
    color: var(--light-color);
    font-family: Forum;
    font-size: 55px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    text-align: center;
    margin-bottom: 141px;
    @include tablet {
      font-size: 50px;
    }
    @include small-tablet {
      font-size: 35px;
    }
    @include mobile {
      margin-bottom: 150px;
    }
  }
  &__title2 {
    margin-bottom: 72px;
    @include tablet {
      margin-bottom: 50px;
    }
  }
  &__block-text {
    display: flex;
    flex-direction: column;
    align-items: center;
    & > .advantages__text {
      margin-bottom: 20px;
    }
  }
  &__image {
    height: 115px;
    margin-bottom: -35px;
    margin-left: -10px;
    @include mobile {
      display: flex;
      justify-content: center;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 80px;
    margin-bottom: 133px;

    @include high-tablet {
      grid-template-columns: repeat(1, 1fr);
      grid-auto-rows: 150px;
      gap: 30px;
    }

    @include mobile {
      margin-bottom: 30px;
    }
  }
  &__item {
    grid-column: 4 span;
    // display: flex;
    // flex-direction: column;

    // z-index: 100;
    // & img {
    //   z-index: -1;
    //   margin-bottom: -24px;
    // }

    // @include high-tablet {
    //   grid-column: 1 span;
    //   gap: px;
    // }

    // @include tablet {
    //   grid-column: 6 span;
    // }
  }

  &__text {
    margin: 0;
    z-index: 1000;
    margin: 0;
    color: var(--light-color);
    text-align: center;
    font-size: 24px;
    font-style: normal;
    font-weight: 300;
    line-height: 131.9%; /* 31.656px */
    @include small-tablet {
      font-size: 18px;
    }
    @include small-tablet {
      text-align: center;
    }
    @include mobile {
      text-align: center;
    }

    & span {
      color: var(--blue-color);
    }
  }

  &__text1,
  &__text2,
  &__text3,
  &__text4,
  &__text5,
  &__text6 {
    position: relative;
  }

  &__item1:hover > &__text1::before {
    animation-name: iconRotate;
    animation-duration: 3s;
    animation-iteration-count: infinite;
    animation-direction: reverse;
    animation-timing-function: ease-in-out;
  }
  &__item2:hover > &__text2::before {
    animation-name: iconRotate;
    animation-duration: 3s;
    animation-iteration-count: infinite;
    animation-direction: reverse;
    animation-timing-function: ease-in-out;
  }
  &__item3:hover > &__text3::before {
    animation-name: iconRotate;
    animation-duration: 3s;
    animation-iteration-count: infinite;
    animation-direction: reverse;
    animation-timing-function: ease-in-out;
  }
  &__item4:hover > &__text4::before {
    animation-name: iconRotate;
    animation-duration: 3s;
    animation-iteration-count: infinite;
    animation-direction: reverse;
    animation-timing-function: ease-in-out;
  }
  &__item5:hover > &__text5::before {
    animation-name: iconRotate;
    animation-duration: 3s;
    animation-iteration-count: infinite;
    animation-direction: reverse;
    animation-timing-function: ease-in-out;
  }
  &__item6:hover > &__text6::before {
    animation-name: iconRotate;
    animation-duration: 3s;
    animation-iteration-count: infinite;
    animation-direction: reverse;
    animation-timing-function: ease-in-out;
  }

  &__text1::before {
    content: "";
    position: absolute;
    width: 115px;
    height: 115px;
    background-image: url("../img/advantages1.png");
    background-size: cover;
    background-repeat: no-repeat;
    z-index: -1;
    top: -70px;
    left: -55px;

    @include high-tablet {
      top: -125px;
      left: 50%;
      margin-left: -70px;
    }
  }
  &__text2::before {
    content: "";
    position: absolute;
    width: 115px;
    height: 115px;
    background-image: url("../img/advantages2.png");
    background-size: cover;
