    z-index: -1;
    top: -70px;
    left: -55px;
    @include high-tablet {
      top: -125px;
      left: 50%;
      margin-left: -70px;
    }
  }
  &__text3::before {
    content: "";
    position: absolute;
    width: 120px;
    height: 115px;
    background-image: url("../img/advantages3.png");
    background-size: cover;
    background-repeat: no-repeat;
    z-index: -1;
    top: -70px;
    left: -55px;
    @include high-tablet {
