      left: 50%;
      margin-left: -70px;
    }
  }
  &__text4::before {
    content: "";
    position: absolute;
    width: 140px;
    height: 120px;
    background-image: url("../img/advantages4.png");
    background-size: cover;
    background-repeat: no-repeat;
