    left: -55px;
    @include high-tablet {
      top: -125px;
      left: 50%;
      margin-left: -70px;
    }
  }
  &__text5::before {
    content: "";
    position: absolute;
    width: 115px;
    height: 115px;
    background-image: url("../img/advantages5.png");
    background-size: cover;
    background-repeat: no-repeat;
    z-index: -1;
    top: -70px;
    left: -55px;
    @include high-tablet {
      top: -125px;
      left: 50%;
      margin-left: -70px;
    }
  }
  &__text6::before {
    content: "";
    position: absolute;
    width: 130px;
    height: 115px;
    background-image: url("../img/advantages6.png");
    background-size: cover;
    background-repeat: no-repeat;
    z-index: -1;
    top: -70px;
    left: -55px;
    @include high-tablet {
      top: -125px;
      left: 50%;
      margin-left: -70px;
    }
  }

  @keyframes iconRotate {
    from {
      transform: rotateY(0deg);
    }
    50% {
      transform: rotateY(90deg);
    }

    to {
      transform: rotateY(0deg);
    }
  }
}
