.clients {
  padding-top: 32px;
  padding-bottom: 100px;
  background-color: var(--dark-color);
  @include mobile {
    padding-bottom: 50px;
  }
  &__title {
    margin: 0;

    color: var(--light-color);
    text-align: center;
    font-family: Forum;
    font-size: 55px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin-bottom: 100px;
    @include tablet {
      font-size: 50px;
    }
    @include small-tablet {
      font-size: 35px;
    }
    @include mobile {
      margin-bottom: 50px;
    }
  }
  &__list {
    background-color: var(--light-color);
    border-radius: 37px;
    padding: 16px 25px;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-auto-flow: row dense;
    gap: 10px;
    align-items: center;

    @include mobile {
      display: none;
    }
  }
  &__image {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__marquee {
    display: none;
    // visibility: hidden;
    background-color: var(--light-color);
    border-radius: 37px;
    padding: 36px 45px;
    & img {
      padding: 0 30px;
    }
    @include mobile {
      display: inline-block;
      padding: 15px 30px;
    }
  }
}
