.report {
  & > .fog {
    background-image: linear-gradient(
        0deg,
        rgba(0, 0, 0, 0) 27.01%,
        #000 116.18%
      ),
      linear-gradient(180deg, rgba(0, 0, 0, 0) 9.01%, #000 258.18%),
      url("../img/stars.png");
    background-repeat: no-repeat;
    background-size: cover;
    padding-top: 30px;
    padding-bottom: 100px;
    @include mobile {
      padding-bottom: 50px;
    }
  }

  &__title {
    margin: 0;
    max-width: 558px;
    color: var(--light-color);
    font-family: Forum;
    font-size: 55px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    @include tablet {
      font-size: 50px;
    }
    @include mobile {
      text-align: center;
      font-size: 35px;
      width: 100%;
    }

    & span {
      color: var(--blue-color);
    }
  }
  &__content {
    position: relative;
    min-height: 761px;
    background-image: url("../img/system.png");
    background-repeat: no-repeat;
    background-size: contain;
    z-index: 1000;
    @include high-desktop {
      // padding-top: 300px;
      background-image: none;
      min-height: min-content;
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-auto-rows: 200px;
      gap: 30px;
    }
    @include small-tablet {
      grid-template-columns: repeat(2, 1fr);
    }
    @include mobile {
      display: flex;
      flex-direction: column;
      align-items: center;

      &::before {
        content: "";
        position: absolute;
        width: 55px;
        height: 55px;
        right: 15%;
        background-image: url("../img/report2.svg");
        background-repeat: no-repeat;
        background-size: contain;
        transform: rotate(100deg);
        // background-size: 60px 60px;
      }
      &::after {
        content: "";
        position: absolute;
        width: 55px;
        height: 55px;
        top: 21%;
        left: 15%;
        background-image: url("../img/report2.svg");
        background-repeat: no-repeat;
        background-size: contain;
        transform: rotate(306deg);
        // background-size: 60px 60px;
      }
    }
  }
  &__text {
    max-width: 300px;
    color: var(--light-color);
    text-align: right;
    font-size: 24px;
    font-style: normal;
    font-weight: 300;
    line-height: 131.9%; /* 31.656px */
    mix-blend-mode: difference;
    @include high-desktop {
      width: 100%;
      text-align: left;
    }
    @include mobile {
      max-width: 100%;
      text-align: center;
    }
  }

  &__item0 {
    display: none;
    @include high-desktop {
      display: block;
      grid-column: 1/2;
      grid-row: 1/2;
      background-image: url("../img/system.png");
      background-repeat: no-repeat;
      background-size: contain;
    }
    @include mobile {
      width: 100%;
      background-position: center;
      height: 200px;
    }
  }
  &__item1 {
    position: absolute;
    top: -35px;
    right: 0;
    @include high-desktop {
      position: relative;
      top: 0;
      grid-column: 2/3;
      grid-row: 1/2;
    }
    @include small-tablet {
      grid-column: 2/3;
      grid-row: 1/2;
    }
  }
  &__item2 {
    position: absolute;
    top: 120px;
    left: -30px;
    @include high-desktop {
      position: relative;
      top: 0;
      left: 0;
      grid-column: 3/4;
      grid-row: 1/2;
    }
    @include small-tablet {
      grid-column: 1/2;
      grid-row: 2/3;
    }
  }
  &__item3 {
    position: absolute;
    top: 245px;
    right: 0px;
    @include high-desktop {
      position: relative;
      top: 0;
      grid-column: 1/2;
      grid-row: 2/3;
    }
    @include small-tablet {
      grid-column: 2/3;
      grid-row: 2/3;
    }
  }
  &__item4 {
    position: absolute;
    width: 210px;
    top: 350px;
    left: -60px;
    @include high-desktop {
      position: relative;
      top: 0;
      left: 0;
      grid-column: 2/3;
      grid-row: 2/3;
    }
    @include small-tablet {
      grid-column: 1/2;
      grid-row: 3/4;
    }
    @include mobile {
      width: 100%;
    }
  }
  &__item5 {
    position: absolute;
    bottom: 0;
    @include high-desktop {
      position: relative;
      grid-column: 3/4;
      grid-row: 2/3;
    }
    @include small-tablet {
      grid-column: 2/3;
      grid-row: 3/4;
    }
  }
  &__text1 {
    display: flex;
    &::after {
      content: "";
      position: absolute;
      width: 60px;
      height: 60px;
      left: 30px;
      bottom: -15px;
      background-image: url("../img/report1.svg");
      background-repeat: no-repeat;
      background-size: 60px 60px;
      @include high-desktop {
        display: none;
      }
    }
  }
  &__text2 {
    display: flex;
    &::after {
      content: "";
      position: absolute;
      width: 60px;
      height: 60px;
      right: -40px;
      top: -37px;
      background-image: url("../img/report2.svg");
      background-repeat: no-repeat;
      background-size: 60px 60px;
      @include high-desktop {
        display: none;
      }
    }
  }
  &__text3 {
    display: flex;
    &::after {
      content: "";
      position: absolute;
      width: 60px;
      height: 60px;
      left: 50%;
      bottom: -30px;
      background-image: url("../img/report3.svg");
      background-repeat: no-repeat;
      background-size: 60px 60px;
      @include high-desktop {
        display: none;
      }
    }
  }
  &__text4 {
    display: flex;
    &::after {
      content: "";
      position: absolute;
      width: 60px;
      height: 60px;
      left: 70%;
      top: -42px;
      background-image: url("../img/report4.svg");
      background-repeat: no-repeat;
      background-size: 60px 60px;
      @include high-desktop {
        display: none;
      }
    }
    @include mobile {
      justify-content: center;
    }
  }
  &__text5 {
    display: flex;
    &::after {
      content: "";
      position: absolute;
      width: 60px;
      height: 60px;
      left: 70%;
      top: -42px;
      background-image: url("../img/report5.svg");
      background-repeat: no-repeat;
      background-size: 60px 60px;
      @include high-desktop {
        display: none;
      }
    }
  }
}
