.technology {
  padding-top: 82px;
  padding-bottom: 130px;
  @include mobile {
    padding-top: 50px;
    padding-bottom: 50px;
  }

  &__content {
    @include flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 155px;
    @include mobile {
      margin-bottom: 40px;
    }
  }
  &__title {
    margin: 0;
    margin-bottom: 80px;
    color: var(--dark-color);
    font-family: Forum;
    font-size: 55px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    @include tablet {
      font-size: 50px;
    }
    @include small-tablet {
      font-size: 35px;
    }
    @include mobile {
      margin-bottom: 50px;
    }
  }
  &__steps {
    display: flex;
    flex-direction: column;
    align-items: center;
    &-title {
      color: var(--dark-color);
      font-family: Forum;
      font-size: 64px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
      margin-bottom: 129px;
      text-align: center;
      @include tablet {
        font-size: 50px;
      }
      @include small-tablet {
        font-size: 35px;
      }
      @include mobile {
        margin-bottom: 30px;
      }
    }
    &-list {
      display: grid;
      position: relative;
      margin-bottom: 50px;
      // gap: 30px;
      // &::after {
      //   content: "";
      //   position: absolute;
      //   margin-right: -2px;
      //   right: 50%;
      //   height: 100%;
      //   border: 2px solid var(--blue-color);
      // }
    }
    &-item {
      display: flex;
      position: relative;
      height: 172px;

      @include mobile {
        height: auto;
      }

      &:nth-child(2n) {
        flex-flow: row-reverse;
      }
      &-content {
        width: 50%;
        padding: 0 30px;
        @include mobile {
          width: 100%;
        }
      }
      &::after {
        content: "";
        position: absolute;
        margin-right: -13px;
        right: 50%;
        width: 26px;
        height: 26px;
        background-color: var(--blue-color);
        border-radius: 50px;

        @include mobile {
          right: 90%;
        }
        // @include mobile {
        //   display: none;
        // }
      }
      &:not(:last-child) {
        &::before {
          content: "";
          position: absolute;
          margin-right: -13px;
          right: 50.8%;
          width: 3px;
          height: 100%;
          background-color: var(--blue-color);
          transform: translateX(-50%);
          @include big-tablet {
            right: 51%;
          }
          @include high-tablet {
            right: 51.3%;
          }
          @include small-tablet {
            right: 51.7%;
          }
          @include mobile {
            right: 92%;
          }
        }
      }

      &-descr {
        color: var(--dark-color);
        font-size: 24px;
        font-style: normal;
        font-weight: 300;
        line-height: 131.9%;
        @include small-tablet {
          font-size: 18px;
        }

        @include mobile {
          padding-left: 50px;
        }
      }
    }
  }
  &_conteyner {
    position: relative;
    padding-bottom: 56.25%; /* задаёт высоту контейнера для 16:9 (если 4:3 — поставьте 75%) */
    height: 0;
    overflow: hidden;
    width: 100%;

    & iframe {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-width: 0;
      outline-width: 0;
    }
  }
}
