.filials {
  padding-top: 50px;
  padding-bottom: 50px;
  &__title {
    margin: 0;
    color: var(--dark-color);
    font-family: Forum;
    font-size: 40px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    text-align: center;
    margin-bottom: 51px;
    @include small-tablet {
      font-size: 35px;
    }
  }

  &__cities {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
    @include small-tablet {
      grid-template-columns: repeat(2, 1fr);
    }
    @include mobile {
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &__city {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__city {
    position: relative;

    font-family: Roboto;
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 112.5%;

    & .filals__city-about {
      visibility: hidden;
      width: 250px;
      background-color: black;
      color: #fff;
      text-align: center;
      border-radius: 6px;
      padding: 5px 0;
      position: absolute;
      z-index: 1;
      // top: -5px;
      // left: 105%;
    }

    &:hover .filals__city-about {
      visibility: visible;
    }
  }
}
