.services {
  background-color: var(--dark-color);
  padding-top: 50px;
  padding-bottom: 100px;
  @include mobile {
    padding-bottom: 50px;
  }

  & > .fog {
    padding-top: 50px;
    background-image: linear-gradient(
        0deg,
        rgba(0, 0, 0, 0) 27.01%,
        #000 146.18%
      ),
      linear-gradient(180deg, rgba(0, 0, 0, 0) 9.01%, #000 258.18%),
      url("../img/stars.png");
    background-repeat: no-repeat;
    background-size: cover;
  }
  &__check--container {
    padding-top: 50px;
    padding-bottom: 50px;
    background-image: linear-gradient(
        0deg,
        rgba(0, 0, 0, 0) 27.01%,
        #000 146.18%
      ),
      linear-gradient(180deg, rgba(0, 0, 0, 0) 9.01%, #000 258.18%),
      url("../img/stars.png");
    background-repeat: no-repeat;
    background-size: cover;
  }
  &__referal--container {
    position: relative;
    padding-top: 50px;
    padding-bottom: 50px;
    background-image: linear-gradient(
        0deg,
        rgba(0, 0, 0, 0) 27.01%,
        #000 98.18%
      ),
      linear-gradient(180deg, rgba(0, 0, 0, 0) 76.01%, #000 89.18%),
      url("../img/stars.png");
    background-repeat: no-repeat;
    background-size: cover;

    &::before {
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      // top: 0;
      // bottom: 0;
      // right: 0;
      // left: 0;
      background-image: url("../img/ref-image.png");
      background-repeat: no-repeat;
      background-size: contain;
      background-position: right;
      margin-top: -49px;
    }
  }

  &__new--container {
    background-image: linear-gradient(
        0deg,
        rgba(0, 0, 0, 0) 27.01%,
        #000 98.18%
      ),
      linear-gradient(180deg, rgba(0, 0, 0, 0) 76.01%, #000 89.18%),
      url("../img/stars.png");
    background-repeat: no-repeat;
    background-size: cover;
  }
  &__check {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 90px;

    &::before {
      content: "";
      position: absolute;
      bottom: -82px;
      right: 0;
      width: 205px;
      height: 229px;
      background-image: url("../img/корзина.png");
      background-repeat: no-repeat;
      background-size: contain;
      @include big-tablet {
        bottom: -100px;
      }
      @include small-tablet {
        display: none;
      }
    }
  }
  &__title {
    margin: 0;
    margin-bottom: 50px;
    color: var(--light-color);
    text-align: center;
    font-family: Forum;
    font-size: 55px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    @include tablet {
      font-size: 50px;
    }
    @include small-tablet {
      font-size: 35px;
    }
  }
  &__post {
    margin: 0;
    color: var(--light-color);
    text-align: center;
    font-family: Roboto;
    font-size: 24px;
    font-style: normal;
    font-weight: 300;
    line-height: 131.9%;
    margin-bottom: 100px;
    @include small-tablet {
      font-size: 18px;
    }
    @include mobile {
      margin-bottom: 50px;
    }
  }
  &__wrapper {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 80px;
    @include high-tablet {
      gap: 30px;
    }
  }
  &__info {
    grid-column: 6 span;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    @include high-tablet {
      grid-column: 12 span;
      align-items: center;
    }
  }
  &__subtitle {
    margin: 0;
    color: var(--light-color);
    // text-align: center;
    font-family: Forum;
    font-size: 30px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin-bottom: 30px;
    @include high-tablet {
      text-align: center;
    }
    @include small-tablet {
      font-size: 25px;
    }
  }

  &__descr {
    margin: 0;
    margin-bottom: 50px;
    color: var(--light-color);
    font-family: Roboto;
    font-size: 24px;
    font-style: normal;
    font-weight: 300;
    line-height: 131.9%;
    @include high-tablet {
      text-align: center;
    }
    @include small-tablet {
      font-size: 18px;
    }
  }

  &__image {
    grid-column: 6 span;
    @include high-tablet {
      grid-column: 12 span;
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }

  &__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 100px;
    margin-bottom: 50px;
    @include high-tablet {
      grid-template-columns: repeat(2, 1fr);
      gap: 50px;
    }
    @include mobile {
      grid-template-columns: repeat(1, 1fr);
      gap: 30px;
    }
  }
  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    &-text {
      color: var(--light-color);
      text-align: center;
      font-size: 24px;
      font-style: normal;
      font-weight: 300;
      line-height: 131.9%; /* 31.656px */
    }
  }

  &__item1:hover > &__item--img1 {
    animation-name: iconRotate;
    animation-delay: 0s;
    animation-duration: 3s;
    animation-iteration-count: infinite;
    animation-direction: reverse;
    animation-timing-function: ease-in-out;
  }
  &__item2:hover > &__item--img2 {
    animation-name: iconRotate;
    animation-delay: 0s;
    animation-duration: 3s;
    animation-iteration-count: infinite;
    animation-direction: reverse;
    animation-timing-function: ease-in-out;
  }
  &__item3:hover > &__item--img3 {
    animation-name: iconRotate;
    animation-delay: 0s;
    animation-duration: 3s;
    animation-iteration-count: infinite;
    animation-direction: reverse;
    animation-timing-function: ease-in-out;
  }
  &__item4:hover > &__item--img4 {
    animation-name: iconRotate;
    animation-delay: 0s;
    animation-duration: 3s;
    animation-iteration-count: infinite;
    animation-direction: reverse;
    animation-timing-function: ease-in-out;
  }
  &__item5:hover > &__item--img5 {
    animation-name: iconRotate;
    animation-delay: 0s;
    animation-duration: 3s;
    animation-iteration-count: infinite;
    animation-direction: reverse;
    animation-timing-function: ease-in-out;
  }
  &__item6:hover > &__item--img6 {
    animation-name: iconRotate;
    animation-delay: 0s;
    animation-duration: 3s;
    animation-iteration-count: infinite;
    animation-direction: reverse;
    animation-timing-function: ease-in-out;
  }
  &__referal {
    display: flex;
    flex-direction: column;
    align-items: center;

    & ol {
      counter-reset: myCounter;
    }
    &--item {
      color: var(--06, #fff);
      font-size: 20px;
      font-style: normal;
      font-weight: 300;
      line-height: 52.5px; /* 150% */
      @include small-tablet {
        line-height: 25.5px;
      }
    }

    &--money {
      border-radius: 48px;
      padding: 10px 35px;
      background: var(--light-color);
      margin-bottom: 50px;
      color: var(--blue-color);
      font-family: Forum;
      text-align: center;
      font-size: 40px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
    }

    &--conditions {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 30px;
      @include high-tablet {
        grid-template-columns: repeat(1, 1fr);
      }
    }

    &--condition {
      border-radius: 48px;
      border: 3px solid #038fd2;
      background: rgba(255, 255, 255, 0);
      padding: 14px 57px;
      color: var(--light-color);
      text-align: center;

      font-size: 25px;
      font-style: normal;
      font-weight: 300;
      line-height: normal;
      & > span {
        color: var(--blue-color);
        font-weight: 700;
      }
    }

    & li {
      list-style: none;
      margin-bottom: 10px;

      &:last-child {
        margin-bottom: 30px;
      }
      @include small-tablet {
        margin-bottom: 20px;
      }
      &::before {
        counter-increment: myCounter;
        content: counter(myCounter);
        color: white;
        background: linear-gradient(
          138deg,
          #038fd2 22.72%,
          rgba(3, 143, 210, 0) 100%
        );
        display: inline-block;
        text-align: center;
        line-height: 40px;
        width: 45px;
        height: 45px;
        border-radius: 50%;
        margin-right: 20px;
      }
    }

    &--item {
      color: var(--light-color);
      font-size: 24px;
      font-style: normal;
      font-weight: 300;
      line-height: 52.5px; /* 150% */
      @include small-tablet {
        font-size: 18px;
        line-height: 25.5px;
      }
    }
  }
  &__faktoring {
    display: flex;
    flex-direction: column;
    align-items: center;

    &--about {
      margin-bottom: 40px;
    }
    &--term {
      margin-bottom: 40px;
    }

    &--subtitle {
      border-radius: 70px;
      border: 1.75px solid #038fd2;

      padding: 22px 50px;
      text-align: center;
      margin-bottom: 40px;
    }
    &--subhead {
      color: var(--blue-color);
      font-family: Forum;
      font-size: 40px;
      font-style: normal;
      font-weight: 400;
      text-align: center;
      line-height: normal;
      margin-bottom: 10px;
    }
    &--item {
      display: flex;
      align-items: center;

      @include mobile {
        align-items: flex-center;
      }
      & img {
        margin-right: 20px;
      }
      & > .services__faktoring--text {
        color: var(--light-color);

        font-size: 24px;
        font-style: normal;
        font-weight: 300;

        @include small-tablet {
          font-size: 18px;
        }
      }
    }
  }
  &__new {
    padding-top: 50px;
    &--title {
      color: var(--blue-color);
      font-family: Forum;
      font-size: 40px;
      font-style: normal;
      font-weight: 400;
      text-align: center;
      line-height: normal;
      margin-bottom: 10px;
    }
    &--text {
      color: var(--06, #fff);
      text-align: center;
      font-family: Roboto;
      font-size: 30px;
      font-style: normal;
      font-weight: 300;
      line-height: 40px; /* 133.333% */
    }
    &--about {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 30px;
      border-radius: 48px;
      border: 2px solid #7bd4ff;
      background: rgba(3, 143, 210, 0);
      padding: 28px 96px;

      @include tablet {
        padding: 28px 50px;
      }

      @include small-tablet {
        grid-template-columns: repeat(1, 1fr);

        padding: 35px;
      }

      &-content {
        display: flex;
        flex-direction: column;
      }
      &-title {
        font-family: Forum;
        color: var(--blue-color);
        font-size: 41px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        margin-bottom: 8px;
      }
      &-text {
        color: var(--light-color);
        // font-family: Roboto;
        font-size: 24px;
        font-style: normal;
        font-weight: 300;
        line-height: normal; /* 250% */
        @include small-tablet {
          font-size: 18px;
        }
      }
    }
    &--links {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 30px;

      @include small-tablet {
        grid-template-columns: repeat(1, 1fr);
      }
    }
    &--link {
      @include tablet {
        display: flex;
        flex-direction: column;
        align-items: center;
      }

      &-content {
        display: flex;
        align-items: flex-end;
        margin-bottom: 28px;

        @include tablet {
          flex-direction: column-reverse;
          align-items: center;
        }
      }
      &-title {
        width: 230px;
        font-family: Forum;
        color: var(--blue-color);
        font-size: 41px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        @include small-tablet {
          text-align: center;
        }
      }
      &-btn {
        border-radius: 48px;
        border: 2px solid #7bd4ff;
        background: linear-gradient(50deg, #038fd2 28%, #56c8ff 75.86%);
        color: var(--light-color);
        padding: 10px 30px;
        font-family: Forum;
        font-size: 40px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;

        @include high-tablet {
          font-size: 30px;
        }
      }
    }
  }
}
