.problems {
  background-color: var(--dark-color);
  padding-top: 130px;

  @include mobile {
    padding-top: 50px;
  }

  &__title {
    margin: 0;
    margin-bottom: 130px;
    font-family: Forum;
    font-size: 55px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    color: var(--light-color);
    @include tablet {
      text-align: center;
      font-size: 50px;
    }
    @include tablet {
      font-size: 35px;
    }
    @include mobile {
      margin-bottom: 50px;
    }
  }
  &__text {
    color: var(--06, #fff);
    font-family: Roboto;
    font-size: 24px;
    font-style: normal;
    font-weight: 400;
    line-height: 106.187%;
    @include small-tablet {
      font-size: 18px;
    }
    @include mobile {
      text-align: left;
    }
  }
  &__solving {
    &-blocks {
      display: grid;
      grid-template-columns: repeat(12, 1fr);
      @include mobile {
        grid-template-columns: repeat(1, 1fr);
        gap: 10px;
        margin-bottom: 50px;
      }
    }
    &-item {
      padding: 30px;
      @include small-tablet {
        padding: 10px;
      }
    }

    &-item1 {
      grid-column-start: 1;
      grid-column-end: 7;
      border-right: 1px solid var(--blue-color);
      border-bottom: 1px solid var(--blue-color);
      @include mobile {
        border: none;
        grid-column: auto;
      }
    }
    &-item2 {
      grid-column-start: 7;
      grid-column-end: 13;
      border-bottom: 1px solid var(--blue-color);
      @include mobile {
        border: none;
        grid-column: auto;
      }
      @include mobile {
        background-color: var(--blue-color);
        margin-left: -16px;
        margin-right: -16px;
        padding: 10px 26px;
      }
    }
    &-item3 {
      grid-column-start: 1;
      grid-column-end: 6;
      border-right: 1px solid var(--blue-color);
      @include mobile {
        border: none;
        grid-column: auto;
      }
      @include mobile {
        background-color: var(--blue-color);
        margin-left: -16px;
        margin-right: -16px;
        padding: 10px 26px;
      }
    }
    &-item4 {
      grid-column-start: 6;
      grid-column-end: 13;
      @include mobile {
        grid-column: auto;
      }
      @include mobile {
        background-color: var(--blue-color);
        margin-left: -16px;
        margin-right: -16px;
        padding: 10px 26px;
      }
    }
    &-title {
      margin: 0;
      color: var(--light-color);
      font-family: Forum;
      font-size: 64px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
      & > span {
        color: var(--blue-color);
      }

      @include high-desktop {
        font-size: 50px;
      }
      @include tablet {
        font-size: 45px;
      }
      @include high-tablet {
        font-size: 40px;
      }
      @include small-tablet {
        font-size: 35px;
      }
    }
    &-descr {
      margin: 0;
      color: var(--light-color);
      font-size: 24px;
      font-style: normal;
      font-weight: 400;
      line-height: 152.188%;
      @include small-tablet {
        font-size: 18px;
      }
    }
  }
}

.swiper {
  width: 100%;
  overflow: visible;
  // overflow: visible !important;
}

.problems__grid {
  display: grid !important;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: 180px;
  gap: 20px;
  margin-bottom: 60px;
  @include tablet {
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
  }
  @include small-tablet {
    grid-auto-rows: 170px;
  }
  @include mobile {
    grid-template-columns: repeat(1, 1fr);
  }
}

.content-card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 20px 22px;
  min-height: 100%;
  color: #fff;
  border-radius: 24px;
  background: #1d1a1b;
  box-shadow: 0px 4px 9px 5px rgba(0, 0, 0, 0.4);
  transition: transform 0.3s ease-in-out;

  &:hover {
    transform: scale(1.2);

    @include mobile {
      transform: scale(1.1);
    }
  }
}
