.thanks {
  background-color: var(--dark-color);
  padding-top: 50px;
  padding-bottom: 116px;
  @include mobile {
    padding-bottom: 50px;
  }
  &__title {
    margin: 0;
    color: var(--light-color);
    text-align: center;
    font-family: Forum;
    font-size: 55px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin-bottom: 119px;
    @include tablet {
      font-size: 50px;
    }
    @include small-tablet {
      font-size: 35px;
    }
    @include mobile {
      margin-bottom: 50px;
    }
  }

  &__picture {
    cursor: url("../img/zoom.svg"), auto;
  }

  &__wrapper {
    max-width: 560px;
  }

  &__blocks {
    display: grid;
    grid-template-columns: repeat(12, 1fr);

    gap: 20px;
  }
  &__block-left {
    grid-column: 6 span;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 10px;
    background-color: var(--light-color);

    @include high-tablet {
      grid-column-start: 3;
      grid-column-end: 11;
    }
    @include small-tablet {
      grid-column-start: 1;
      grid-column-end: 13;
    }
  }
  &__block-right {
    grid-column: 6 span;
    @include high-tablet {
      grid-column-start: 3;
      grid-column-end: 11;
    }
    @include small-tablet {
      grid-column-start: 1;
      grid-column-end: 13;
    }
  }
  &__images {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 3px;
  }
  &__photo {
    cursor: pointer;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 100;
  }
  &__modal {
    height: 90% !important;
  }
}
.btns__navigate {
  display: flex;
  @include small-tablet {
    justify-content: center;
  }

  @include mobile {
    padding-bottom: 5px;
    & .btn-next > svg {
      width: 25px;
      height: 25px;
    }
    & .btn-prev > svg {
      width: 25px;
      height: 25px;
    }
  }
}

.btn-nav {
  &:not(:last-child) {
    margin-right: 10px;
  }
  &:hover svg {
    fill: var(--accent-color);
  }
}

.swiper-button-disabled {
  &:hover svg {
    fill: gray;
    cursor: none;
  }
  & svg {
    fill: gray;
    opacity: 0.2;
  }
}
