.questions {
  background-color: var(--dark-color);
  padding-top: 115px;
  padding-bottom: 70px;
  @include mobile {
    padding-top: 50px;
    padding-bottom: 50px;
  }
  &__title {
    margin: 0;
    color: var(--light-color);
    text-align: center;
    font-family: Forum;
    font-size: 55px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin-bottom: 110px;
    @include tablet {
      font-size: 50px;
    }
    @include small-tablet {
      font-size: 35px;
      margin-bottom: 50px;
    }
  }
}
.accordion {
  &-button {
    background-color: var(--dark-color);
    color: var(--blue-color);
    font-size: 24px;

    &::after {
      background-image: url("../img/arrow.svg");
    }

    &:hover {
      background-color: var(--blue-color);
      color: var(--light-color);
      &::after {
        background-image: url("../img/arrow2.svg");
      }
    }
    @include small-tablet {
      font-size: 18px;
    }
  }
  &-button:not(.collapsed)::after {
    background-image: url("../img/arrow.svg");
    transform: var(--bs-accordion-btn-icon-transform);
  }
  &-button:hover:not(.collapsed)::after {
    background-image: url("../img/arrow2.svg");
    transform: var(--bs-accordion-btn-icon-transform);
  }

  &-body {
    font-size: 24px;
    font-style: normal;
    font-weight: 300;
    line-height: 131.9%;
    @include small-tablet {
      font-size: 18px;
    }
  }
}
