.about {
  background-color: var(--dark-color);
  padding-top: 130px;
  @include mobile {
    padding-top: 50px;
    padding-bottom: 50px;
  }
  &__title {
    margin: 0;
    text-align: center;
    font-family: Forum;
    font-size: 55px;
    font-style: normal;
    line-height: normal;
    margin-bottom: 70px;
    color: var(--light-color);
    @include tablet {
      font-size: 50px;
    }
    @include small-tablet {
      font-size: 35px;
    }
  }
  &__content {
    display: flex;
    justify-content: center;
  }
  &__descr {
    margin: 0;
    text-align: center;
    max-width: 645px;
    color: var(--light-color);
    font-size: 24px;
    font-style: normal;
    font-weight: 200;
    line-height: 152.188%;
    margin-bottom: 70px;
    @include small-tablet {
      font-size: 18px;
    }
  }
  &__working {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-auto-rows: minmax(645px, auto);
    @include tablet {
      grid-template-columns: repeat(3, 1fr);
      grid-auto-rows: auto;
      gap: 30px;
    }
    @include small-tablet {
      grid-template-columns: repeat(12, 1fr);

      font-size: 35px;
    }
    &-text {
      font-size: 18px;
      font-style: normal;
      font-weight: 200;
      line-height: 135.188%;
      color: var(--light-color);
    }
    &-left {
      position: relative;
      display: flex;
      flex-direction: column;
      &::after {
        content: "";
        position: absolute;
        top: 161px;
        left: 69px;
        width: 322px;
        height: 110px;
        background-image: url("../img/left_line.svg");
        background-size: 322px 110px;
        @include big-tablet {
          right: 100px;
          top: 190px;
        }

        @include tablet {
          display: none;
        }
      }
      @include tablet {
        align-items: center;
        // text-align: center;
        & img {
          margin-bottom: 5px;
        }
      }
      @include mobile {
        & > span {
          text-align: center;
        }
      }
    }
    &-rigth {
      position: relative;
      display: flex;
      flex-direction: column;
      &::before {
        content: "";
        position: absolute;
        top: 300px;
        right: 200px;
        width: 322px;
        height: 110px;
        background-image: url("../img/rigth_line.svg");
        background-size: 322px 110px;

        @include high-desktop {
          right: 150px;
        }

        @include big-tablet {
          top: 255px;
        }

        @include tablet {
          display: none;
        }
      }
      @include tablet {
        align-items: center;
      }
      @include small-tablet {
        & span {
          text-align: center;
        }
      }

      & img {
        padding-top: 150%;
        @include tablet {
          padding-top: 0;
          margin-bottom: 20px;
        }
      }
    }
    &-text1 {
      grid-column-start: 1;
      grid-column-end: 3;
      @include tablet {
        grid-column: 1 span;
      }
      @include small-tablet {
        grid-column: 1/13;
      }
    }
    &-text2 {
      position: relative;
      grid-column-start: 3;
      grid-column-end: 10;
      background-image: url("../img/about_logo.png");
      background-repeat: no-repeat;
      background-size: contain;
      @include tablet {
        grid-column: 1 span;
        background-image: none;
      }
      @include small-tablet {
        grid-column: 1/13;
      }
    }
    &-text3 {
      grid-column-start: 10;
      grid-column-end: 13;
      @include tablet {
        grid-column: 1 span;
      }
      @include small-tablet {
        grid-column: 1/13;
      }
    }
    &-descr {
      display: flex;
      flex-direction: column;
      position: absolute;
      bottom: 30%;
      left: 25%;
      max-width: 336px;
      @include tablet {
        align-items: center;
      }
      @include small-tablet {
        text-align: center;
        max-width: none;
      }
      & img {
        margin-right: auto;
        margin-left: auto;
        @include tablet {
          margin-right: 0;
          margin-left: 0;
        }
      }

      @include tablet {
        position: relative;
        bottom: 0%;
        left: 0%;
      }

      // &::before {
      //   content: "";
      //   display: block;
      //   width: 83px;
      //   height: 85px;
      //   position: absolute;
      //   left: 50%;
      //   margin-left: -55px;
      //   top: -100px;
      //   background-image: url("../img/logo_human.svg");
      //   background-size: 83px 85px;
      // }
    }
  }
}
