.support {
  background-color: var(--dark-color);
  padding-top: 30px;
  padding-bottom: 100px;
  @include mobile {
    padding-bottom: 50px;
  }
  &__content {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: auto;
    gap: 30px;

    @include tablet {
      grid-template-columns: repeat(12, 1fr);
    }
    @include mobile {
      grid-template-columns: repeat(1, 1fr);
    }
  }
  &__item1 {
    grid-column-start: 1;
    grid-column-end: 5;
    grid-row-start: 1;
    grid-row-end: 2;
    @include tablet {
      grid-column-start: 1;
      grid-column-end: 5;
      grid-row-start: 2;
      grid-row-end: 3;
    }
    @include high-tablet {
      grid-column-start: 1;
      grid-column-end: 6;
      grid-row-start: 2;
      grid-row-end: 3;
    }
    @include mobile {
      grid-column: 1 span;
      grid-row: 2/3;
      width: 90dvw;
    }
  }
  &__item2 {
    grid-column-start: 1;
    grid-column-end: 13;
    grid-row-start: 1;
    grid-row-end: 5;
    padding: 117px 160px;
    // border-left: 1px solid var(--light-color);
    // border-right: 1px solid var(--light-color);
    // border-radius: 525.114px;

    @include big-tablet {
      padding: 117px 110px;
    }
    @include tablet {
      padding: 50px 110px;
      grid-row-start: 1;
      grid-row-end: 2;
    }
    @include small-tablet {
      padding: 20px 110px;
    }
    @include mobile {
      padding: 0;
      border: none;
      grid-column: 1;
      grid-row: 1/2;
      width: 90dvw;
    }
  }
  &__item3 {
    grid-column-start: 9;
    grid-column-end: 13;
    grid-row-start: 3;
    grid-row-end: 4;
    @include tablet {
      grid-column-start: 9;
      grid-column-end: 13;
      grid-row-start: 2;
      grid-row-end: 3;
    }
    @include high-tablet {
      grid-column-start: 8;
      grid-column-end: 13;
      grid-row-start: 2;
      grid-row-end: 3;
    }
    @include mobile {
      grid-column: 1;
      grid-row: 3/4;
      width: 90dvw;
    }
  }
  &__item4 {
    grid-column-start: 3;
    grid-column-end: 8;
    grid-row-start: 4;
    grid-row-end: 5;
    @include tablet {
      grid-column-start: 5;
      grid-column-end: 9;
      grid-row-start: 3;
      grid-row-end: 4;
    }
    @include high-tablet {
      grid-column-start: 4;
      grid-column-end: 10;
      grid-row-start: 3;
      grid-row-end: 4;
    }
    @include mobile {
      grid-column: 1;
      grid-row: 4/5;
      width: 90dvw;
    }
  }
  &__text {
    color: var(--light-color);
    font-family: Roboto;
    font-size: 24px;
    font-style: normal;
    font-weight: 300;
    line-height: 131.9%;
    & span {
      color: var(--blue-color);
    }
    @include small-tablet {
      font-size: 18px;
    }
  }
  &__title {
    text-align: center;
    position: relative;
    font-family: Forum;
    font-size: 50px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    padding: 117px 263px;

    // border-left: 1px solid var(--light-color);
    // border-right: 1px solid var(--light-color);
    // border-radius: 525.114px;

    &::before {
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      border-left: 1px solid var(--light-color);
      border-right: 1px solid var(--light-color);
      border-radius: 525.114px;

      animation-name: borderSupport;
      animation-duration: 3s;
      animation-iteration-count: infinite;
      // animation-direction: reverse;
      animation-timing-function: ease-in-out;
    }
    @include big-tablet {
      padding: 117px 170px;
    }
    @include tablet {
      padding: 50px 50px;
    }
    @include small-tablet {
      padding: 15px 50px;
      font-size: 30px;
    }
  }
}

@keyframes borderSupport {
  from {
    transform: scale(0.5);
  }

  to {
    transform: scale(1.5);
  }
}
