.banks {
  background-color: var(--dark-color);
  padding-top: 50px;
  padding-bottom: 116px;
  @include mobile {
    padding-bottom: 50px;
  }
  &__title {
    margin: 0;
    color: var(--light-color);
    font-family: Forum;
    font-size: 55px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    text-align: center;
    margin-bottom: 100px;
    @include tablet {
      font-size: 50px;
    }
    @include small-tablet {
      font-size: 35px;
      margin-bottom: 50px;
    }

    &-blue {
      color: var(--blue-color);
      margin-bottom: 30px;
    }
  }
  &__wrapper {
    padding: 30px;
    background-color: var(--light-color);
    border-radius: 37px;
    &:not(:last-child) {
      margin-bottom: 100px;
      @include mobile {
        margin-bottom: 50px;
      }
    }

    @include mobile {
      border-radius: 0;
      padding: 5px;
    }
  }

  &__images-bel {
    @include mobile {
      // padding: 0 20px;
      // min-height: 40px;
      // min-width: 100px;
    }
  }
  &__content {
    width: 100% !important;
  }
  &__item {
    display: flex;
    justify-content: center;
  }
}
.flex-wrapper {
  display: flex;
  align-items: center;
}
