.fog {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: var(--dark-color);
  background-size: cover;
}
.fog::before,
.fog::after,
.fog > .foging::before,
.fog > .foging::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 2500.625em;
  background-size: cover;
  background-position: center;
  background-repeat: repeat-x;

  // @include mobile {
  //   display: none;
  // }
}
.fog .foging::before,
.fog .foging::after {
  left: 100%;
}
.fog::before,
.fog .foging::before {
  background-image: url("https://www.aktivkredit.ru/ac/assets/img/clouds.png");
  animation: fogmove 800s linear 0s infinite;
}
// .fog::after,
