.footer {
  background-color: var(--blue-color);
  padding-top: 39px;
  padding-bottom: 30px;
  &__container {
    display: flex;
    justify-content: space-between;
    @include small-tablet {
      flex-direction: column;
    }
  }
  &__contacts {
    display: flex;
    flex-direction: column;
    color: var(--light-color);
    font-family: Roboto;
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 112.5%;
    &-name,
    &-ogrn,
    &-inn {
      margin-bottom: 20px;
    }
    @include small-tablet {
      margin-bottom: 30px;
    }
  }
  &__doc {
    color: var(--light-color);
    font-family: Roboto;
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 112.5%;
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }
  &__icon {
    &:not(:last-child) {
      margin-right: 10px;
    }
    &:hover svg {
      fill: var(--accent-color);
    }
  }

  &__social {
    display: flex;
    flex-direction: column;
    &-list {
      margin-bottom: 39px;
    }
  }
  &__link {
    color: var(--light-color);
    font-family: Roboto;
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 112.5%;
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }
}
