.hero {
  padding-top: 35dvh;
  min-height: 100dvh;
  background-color: var(--dark-color);
  background-image: url("https://www.aktivkredit.ru/ac/assets/img/antoine-barres.jpg");
  background-repeat: no-repeat;
  background-size: cover;

  @include high-desktop {
    background-position: bottom center;
  }

  @include mobile {
    padding-top: 290px;
    padding-bottom: 60px;
    min-height: auto;
  }

  &__container {
    @include flex;
    flex-direction: column;
    align-items: flex-start;
  }

  &__content {
    max-width: 530px;
  }

  &__title {
    margin: 0;
    max-width: 530px;
    font-family: Forum;
    font-size: 64px;
    font-weight: 400;
    margin-bottom: 50px;

    color: var(--light-color);
    &-text {
      display: block;
      max-width: 90%;
      font-size: 24px;
      @include small-tablet {
        font-size: 18px;
      }
    }
    @include small-tablet {
      font-size: 35px;
    }
  }
  &__footer {
    position: absolute;
    bottom: -50px;
    width: 100%;
    height: 340px;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000 100%);
    @include mobile {
      display: none;
    }
  }
}
